import { PARAMETERS } from '@angular/core/src/util/decorators';

const baseUrl:string = "http://localhost:8085/spotify";
const songs:string = "/songs";
const song:string = "/song";
const songById = "/song/***";


export class urls{
    
    public static createUrlQuery(queryParam:Object):string{
        let params:string ="?";
        for(let param of Object.keys(queryParam)){
            params = params.concat(param+"="+queryParam[param]+"&");
        }
        params = params.slice(0,params.length-1);

        return baseUrl.concat(songs+params); 
    }


    public static createUrlPathVariable(path:string):string{
        return baseUrl.concat(songById).replace("***",path);
    }
    
}